.status {
    font-size: 12px;
}

.follow {
    margin-top: 0;
    width: 100%;
    height: 15px;
    font-size: 10px;
    background-color: #00aa66;
    color: #fff;
    border: none;
    cursor: pointer;
}

.unfollow {
    width: 100%;
    height: 15px;
    font-size: 10px;
    background-color: #7f0887;
    color: #fff;
    border: none;
    cursor: pointer;
}

.hide {
    opacity: 30%;
    display: table;
    width: 200px;
    background-color: #e2e2e2;
    text-align: center;
    height: 260px;
    float: left;
    margin: 12px;
}

.hide a {
    color: #666;
    text-decoration: none;
}


